export default function(values) {
    let errors = {}

    if (values.name.trim() === '') {
        errors.name = 'Name must not be empty'
    }

    if (values.email.trim() === '') {
        errors.email = 'Email must not be empty'
    } else {
        //eslint-disable-next-line
        let pattern = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
        let emailValid = values.email.match(pattern)
        if(!emailValid) errors.email = 'Please enter a valid email id'
    }

    if (values.password.trim() === '') {
        errors.email = 'Password must not be empty'
    } else if(values.password.trim() !== values.confirmPassword.trim()) {
        errors.password = "Password doesn't match"
    } else if (values.password.trim().length < 6) {
        errors.password = "Password must be more than 6 character"
    }

    return errors
}