<template>
    <v-container class="cont">

        <div class="login-header d-flex justify-end mb-5">
            <img src="../assets/backb.png" @click="$router.go(-1)"
              style="height: 2rem; width: auto; cursor: pointer" class="mr-4"
            />
        </div>
        <v-img contain class="mb-5" src="../assets/new_files/logo.png" id="logo"></v-img>

        <div class="box justify-center">
            <v-tabs
              class="tab-box"
              hide-slider
              grow
              height="53"
            >
              <v-tab :class="[activeTab == 1 ? 'primary white--text' : 'primary--text', 'tab1 font-weight-regular']" @click="changeTab(1)">Sign in</v-tab>
              <v-tab :class="[activeTab == 2 ? 'primary white--text' : 'primary--text', 'tab2 font-weight-regular']" @click="changeTab(2)">Sign up</v-tab>
            </v-tabs>

            <!-- tab 1 content-->
            
            <div v-if="activeTab == 1">
              <div class="title--text titlet font-weight-bold mt-4">Sign in to your account</div>
              <div class="para--text sub-text mt-3">Sign in to your account to start receiving job alerts. Dont worry - its free.</div>
              
              <form class="mt-4 mb-2">
                <div>
                  <label class="ct-label">Email Address</label>
                  <div class="input-box d-flex align-center mt-1">
                    <input class="inputs" ref="email" v-model="loginData.email"  />
                  </div>
                </div>

                <div class="mt-4">
                  <label class="ct-label">Password</label>
                  <div class="input-box d-flex align-center mt-1">
                    <input class="inputs" type="password" ref="password" v-model="loginData.password" />
                  </div>
                </div>
              

                <label class="ct-label">Forgot your password?</label>

                <div class="d-flex align-center mt-6">
                  <v-checkbox v-model="loginData.term" class="check-b" dense></v-checkbox>
                  <div class="para--text" style="font-size: 0.8rem">I accept your <span class="primary--text font-weight-bold">Terms of Use</span>  & <span class="primary--text font-weight-bold">Privacy Policy.</span></div>
                </div>

                <v-btn height="48" 
                  elevation="0" dark
                  color="#0046FE" 
                  style="border-radius: 10px"
                  @click="loginUser"
                  class="text-capitalize font-weight-regular ct-btn-border mt-4">
                  <span class="d-flex align-center justify-center">
                      <span style="font-size: 0.8rem; text-transform: none !important;" class="px-2">
                        Sign in
                      </span>
                  </span>
                </v-btn>
              </form>

              <div class="mt-4">
                <div class="para--text ct-or">Or</div>
                <div class="ct-line"></div>
              </div>

              <!-- social login -->
              <div class="mt-9" >
                <div class="d-flex justify-center" > 

                  <span @click="socialLogin('FacebookAuthProvider')">
                    <img src="../assets/social/facebook.png" style="height: 2rem; width: auto; cursor: pointer" class="mr-3" />
                  </span>  
                       
                  <a href="/api/auth/linkedin">
                    <img src="../assets/social/linkedin.png" style="height: 2rem; width: auto; cursor: pointer" class="mr-3" />    
                  </a>     

                  <span @click="socialLogin('GithubAuthProvider')">
                    <img src="../assets/social/github.png" style="height: 2rem; width: auto; cursor: pointer" class="mr-3" />
                  </span>   
                  
                  <span @click="socialLogin('GoogleAuthProvider')">
                    <img src="../assets/social/google.png" style="height: 2rem; width: auto; cursor: pointer" />  
                  </span>
                           
                </div>
              </div>
            </div>


            <!-- tab 2 content-->


            <div v-if="activeTab == 2">
              <div class="title--text titlet font-weight-bold mt-4">Create New Account</div>
              <div class="para--text sub-text mt-3">To join our search engine create a new account. Don't worry - it's free.</div>
              
              <form class="mt-4 mb-2">
                <div>
                  <label class="ct-label">Full Name</label>
                  <div class="input-box d-flex align-center mt-1">
                    <input class="inputs" v-model="signUpData.name" />
                  </div>
                </div>

                <div class="mt-4">
                  <label class="ct-label">Email Address</label>
                  <div class="input-box d-flex align-center mt-1">
                    <input class="inputs" v-model="signUpData.email" />
                  </div>
                </div>

                <v-row dense>
                  <v-col class="mr-md-1">
                    <div class="mt-4 flex-1" style="width: auto">
                      <label class="ct-label">Password</label>
                      <div class="input-box d-flex align-center mt-1">
                        <input class="inputs" type="password" v-model="signUpData.password" />
                      </div>
                    </div>
                  </v-col>

                  <v-col class="ml-md-1">
                  <div class="mt-4">
                    <label class="ct-label">Confirm Password</label>
                    <div class="input-box d-flex align-center mt-1">
                      <input class="inputs" type="password" v-model="signUpData.confirmPassword" />
                    </div>
                  </div>
                  </v-col>
                </v-row>
              


                <div class="d-flex align-center mt-2">
                  <v-checkbox v-model="signUpData.term" class="check-b" dense></v-checkbox>
                  <div class="para--text" style="font-size: 0.8rem">I accept your <span class="primary--text font-weight-bold">Terms of Use</span>  & <span class="primary--text font-weight-bold">Privacy Policy.</span></div>
                </div>

                <v-btn height="48" 
                    elevation="0" dark  
                    color="#0046FE" 
                    style="border-radius: 10px"
                    @click="registerUser"
                    class="text-capitalize font-weight-regular ct-btn-border mt-4">
                    <span class="d-flex align-center justify-center">
                        <span style="font-size: 0.8rem; text-transform: none !important;" class="px-2">
                            Create Account
                        </span>
                  </span>
                </v-btn>

              </form>

              <div class="d-flex align-center mt-8">                
                <div class="para--text" style="font-size: 0.8rem">Already registered?<span class="primary--text font-weight-bold ml-1" style="cursor: pointer" @click="activeTab = 1">Sign In</span></div>
              </div>

              <div class="mt-4">
                <div class="para--text ct-or">Or</div>
                <div class="ct-line"></div>
              </div>

              <!-- social login -->
              <div class="mt-9" >
                <div class="d-flex justify-center" > 

                  <span @click="socialLogin('FacebookAuthProvider')">
                    <img src="../assets/social/facebook.png" style="height: 2rem; width: auto; cursor: pointer" class="mr-3" />
                  </span>  
                       
                  <a href="/api/auth/linkedin">
                    <img src="../assets/social/linkedin.png" style="height: 2rem; width: auto; cursor: pointer" class="mr-3" />    
                  </a>     

                  <span @click="socialLogin('GithubAuthProvider')">
                    <img src="../assets/social/github.png" style="height: 2rem; width: auto; cursor: pointer" class="mr-3" />
                  </span>   
                  
                  <span @click="socialLogin('GoogleAuthProvider')">
                    <img src="../assets/social/google.png" style="height: 2rem; width: auto; cursor: pointer" />  
                  </span>
                           
                </div>
              </div>
            </div>


            <!-- Error tags -->
            <div v-if="getErrors.length > 0" class="mt-8">    

               <v-alert v-for="(err, index) in getErrors" :key="index"
                 dense
                 outlined
                  type="error"
                >
                {{err.msg}}
              </v-alert>
            </div>
        </div>

        
    </v-container>
</template>

<script>
  import firebase from '../firebase'

  import { mapActions } from 'vuex'
  import validLogin from '../validation/validLogin'
  import validRegister from '../validation/validRegister'
  import setAuthUser from '../services/setAuthUser'
  // import setAuthToken from '../services/setAuthToken'
  import axios from 'axios'
  // import router from '../router'

  export default {
    data: () => ({
      errors: {},
      activeTab: 1,
      loginData: {
        email: '',
        password: '',
        term: false
      },
      signUpData: {
        name: '',
        email: '',
        password: '',
        confirmPassword: '',
        term: false
      }
    }),
    computed: {
        getErrors() {
          return this.errors
        }
    },
    methods: {
      ...mapActions(['fetchUserData']),
      changeTab(tab) {
        // this.errors([])
        this.activeTab = tab
      },
      loginUser() {

        if(Object.keys(validLogin(this.loginData)) && !this.loginData.term) {
          this.errors = validLogin(this.loginData)
          return
        }

        this.authApiRequest(this.loginData, 'login')
      },
      registerUser() {
        if(Object.keys(validRegister(this.signUpData)) && !this.signUpData.term) {
          this.errors = validLogin(this.signUpData)
          return
        }

        this.authApiRequest(this.signUpData, 'register')
      },
      async authApiRequest(data, route) {
        this.$store.commit('setLoading', true)
        try {
          let response = await axios.post(`/api/auth/${route}`, data)
          setAuthUser(response.data.token)
        } catch (err) {
          console.log("Err:", err)
          this.$store.commit('setLoading', false)
        }
      },
      socialLogin(providerName) {
        let provider = new firebase.auth[providerName]()
        if(providerName === 'GithubAuthProvider') {
          provider.addScope('read:user')
          provider.addScope('user:displayName')
          provider.addScope('user:email')
        }

        firebase.auth().signInWithPopup(provider).then((result) => {
          console.log("check identify: ", result)
          const { profile } = result.additionalUserInfo
          const  requestData = {}

          switch(providerName) {
            case 'GoogleAuthProvider':
              requestData.name = profile.name
              requestData.email = profile.email
              requestData.avatar = profile.picture
              break
            case 'FacebookAuthProvider':
              requestData.name = profile.name
              requestData.email = profile.email
              requestData.avatar = profile.picture.data.url
              break
            case 'GithubAuthProvider':
              requestData.name = profile.login
              requestData.email = result.user.email
              requestData.avatar = result.user.photoURL
              break
            default:
              return 
          }

          axios.post('/api/auth/social', requestData)
            .then(result => setAuthUser(result.data.token))
        }).catch((err) => {
          console.log("Auth Error: ",err)
        })
      }
    },
  }
</script>

<style scoped>
  #logo {
    height: 2.2rem;
    width: auto
  }
  .cont {
      padding: 2rem 0;
  }

  .box {
      border: solid 2px #EBEFF5;
      padding: 3rem;
      padding-top: 2rem;
      padding-bottom: 2rem;
      max-width: 35rem;
      margin: 0 auto;    
  }

  /* tab style */

  /* .tab-box {
    border-radius: 10px; 
    border: solid 2px #0046FE;
    padding: 0!important;
    margin: 0;
  } */

  .tab1 {
    border-bottom-left-radius: 6px;
    border-top-left-radius: 6px;
    border: solid 2px #0046FE;
    text-transform: none !important;
  }
  .tab2 {
    border-bottom-right-radius: 6px;
    border-top-right-radius: 6px;
    text-transform: none !important;
    border: solid 2px #0046FE;
  }

  /* tab style end */

  /* info */
  .titlet {
    text-align: center;
    font-size: 1.2rem;
  }

  .sub-text {
    font-size: 0.9rem;
    text-align: center;
    max-width: 20rem;
    margin: 0 auto;
  }

  /* form style start */

  .ct-label {
    font-size: 13px;
    color: #838CA3
  }

  .input-box {
    border: solid 2px #EBEFF5;
    border-radius: 8px;
    height: 3.3rem;
    background-color: #FDFDFD;
    padding-left: 1rem;
  }

  .inputs {
    flex: 1;
    outline: none;
    color: #838CA3;
    font-size: 14px;
    font-weight: 500;
    background-color: #FDFDFD;
  }

  /* check box */
  .check-b {
    height: 1.5rem;
    width: 1.2rem;
    margin: 0 !important;
    padding: 0 1.6rem 0 0!important;
    opacity: 0.8;
    transform: translateX(-4px);
  }

  /* divider line */

  .ct-or {
    font-size: 0.9rem;
    text-align: center;
    transform: translateY(9px);
    margin: 0 auto;
    background-color: white;
    max-width: 1.4rem;
  }

  .ct-line {
    height: 1px;
    
    width: 100%;
    background-color: #EBEFF5;
  }

  @media only screen and (max-width: 600px) {
  /* For mobile phones: */
  
  #logo {
    height: 2rem;
    width: auto;
  }

  .box {
    padding: 1.6rem
  }
  
}

  
</style>